const RootStyle = styled('div')(({ theme }) => ({
  textAlign: 'center',
  padding: theme.spacing(10, 0),
  backgroundSize: 'cover',
  backgroundImage: `linear-gradient(to right, ${alpha(theme.palette.grey[50], 0.9)} , ${alpha(
    theme.palette.grey[50],
    0.9
  )}),url(https://images.unsplash.com/photo-1674104185750-c38a364772f7?q=80&w=1374&auto=format&fit=crop&ixlib=rb-4.0.3&ixid=M3wxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8fA%3D%3D)
  `,
  // opacity:0.9,
  [theme.breakpoints.up('md')]: {
    textAlign: 'left',
    padding: 0,
    // height: 840,
    overflow: 'hidden',
  },
}));

