import { m } from 'framer-motion';
// @mui
import { styled } from '@mui/material/styles';
import { Box, Container, Typography } from '@mui/material';
// components
import { MotionContainer, TextAnimate, varFade } from '../../components/animate';

// ----------------------------------------------------------------------

const RootStyle = styled('div')(({ theme }) => ({
  backgroundSize: 'cover',
  backgroundPosition: 'center',
  backgroundImage:
    'url(https://minimal-assets-api.vercel.app/assets/images/about/testimonials.jpg), url(https://minimal-assets-api.vercel.app/assets/images/about/testimonials.jpg)',
  padding: theme.spacing(10, 0),
  [theme.breakpoints.up('md')]: {
    height: 560,
    padding: 0,
  },
}));

const ContentStyle = styled('div')(({ theme }) => ({
  textAlign: 'center',
  [theme.breakpoints.up('md')]: {
    textAlign: 'left',
    position: 'absolute',
    bottom: theme.spacing(10),
  },
}));

// ----------------------------------------------------------------------

export default function ServicesOtherServicesHero() {
  return (
    <RootStyle>
      <Container component={MotionContainer} sx={{ position: 'relative', height: '83%' }}>
        <ContentStyle>
          {/* <TextAnimate text="Other Services from CCMS" sx={{ color: 'primary.main' }} variants={varFade().inRight} /> */}
          <Box sx={{ display: 'block', color: 'primary.main' }}>
            <TextAnimate text="Other" sx={{ mr: 2 }} />
            <TextAnimate text="Services" sx={{ mr: 2 }} />
            <TextAnimate text="from" sx={{ mr: 2 }} />
            <TextAnimate text="CCMS" />
          </Box>

          <m.div variants={varFade().inRight}>
            <Typography
              variant="h4"
              sx={{
                mt: 5,
                color: 'common.white',
                fontWeight: 'fontWeightMedium',
              }}
            >CCMS offers a wide range of non-title insurance services.</Typography>
          </m.div>
        </ContentStyle>
      </Container>
    </RootStyle>
  );
}
